.base {
    border: 0px solid black;
    width:100%;
    height: 100%;
    background-color: #fff;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.principal td{
    border: 0px solid red;
}
.header, .footer{
    border: 0px solid #000;
    background-color: #fff;
    margin: 10px;
    text-align: center;
}
.container {
    border: 0px solid black;
    background-color: #fff;
    padding: 20px;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
}
table {
    width: 100%;
    max-width: 800px;
    background-color: white;
    align-content: center;
    margin: 0 auto;    
}
.tableResultado{
    border: 0   px solid red;
    margin-bottom:5%;
}

.tableResultado td{
    border-bottom: 1px solid #999;
    border-right: 1px solid #999;
    padding: 5px
}
.conteudo{
    padding-top:0%;
    padding-bottom:10%;
}
.campos {
    border: 2px solid red;
    background-color: blue;
    padding: 20px;
    margin: 0 auto;
    width: 100%;
}
input{
    height:30px;
    width:100px;
}
td{
    border: 0px solid #000;
}
.colNivel{
    width: 20px;
}
button{
    height: 40px;
    width: 100%;
}
.popUp, .popUpLogin{
    border: 1px solid #000;
    background-color: #231225;
    width: 400px;
    padding: 30px;
    text-align: center;
    margin-top: 100px    
}
.popUpLogin p{
    color: #fff;
    text-align: left
}
.popUpLogin td{
    color: #fff;
    text-align: center
}
.popUpLogin h3{
    color: #fff;
    text-decoration: underline
}
.logo{
    width:50%
}
.popUp p{
    text-align: left;
}
.campoLogon{
    width: 100%;
}
.botao{
    height: 40px;
    width: 100%;
    background-color: #FDD900;
    text-align: center;
    vertical-align:auto;
    color: #000;
    padding-top: 20px;
    border: 1px solid #000;
    font-weight: bold;
}
.botaoAction{
    height: 40px;
    width: 100%;
    background-color: #FDD900;
    text-align: center;
    vertical-align:auto;
    color: #000;
    border: 1px solid #000;
    font-weight: bold;
    margin-top: 20px;
}
select{
    height: 40px;
    width: 100%;
}
.menu{
    border: 1px solid #000;    
    padding: 5px;
    text-align: center;
    text-decoration: none;
    background-color: rgb(59, 56, 56);
    color:#fff
}
.menu:active{
    border: 1px solid #000;
    text-align: center;
    text-decoration: none;
    background-color: #FDD900;
    color:#000
}
a{
    color: #000;
    text-decoration: none;
}
.botaoMenu td{
    border: 1px solid rgb(59, 56, 56);
    text-align: center;
    text-decoration: none;
    padding-left: 80px;
    padding-right: 80px;
}
a, Link{
    color:#fff;
    text-align: center;
}
a p{
    color:#333;
    font-weight: bold;
    text-align: center;    
}
.entrarCampo{
    text-align: center;   
}
.entrarCampo h4{
    color:#333;
    text-decoration: underline;
}
.botaoSair{
    color:#333;
    text-decoration: underline;
}
.erro{
    color: red;
    text-align: center;
}